    import React, { useState, useEffect } from 'react';
    import axios from 'axios';
    import styled from 'styled-components';
    import { useNavigate } from 'react-router-dom';

    import Header from "../../components/domain/Header";
    import Navigation from "../../components/domain/Navigation";

    const Board = () => {
        const navigate = useNavigate();
        const [boardList, setBoardList] = useState([]);

        useEffect(() => {
            const fetchData = async () => {
                try {
                    const response = await axios.get('/boards');
                    setBoardList(response.data.boardList);
                } catch (error) {
                    console.error(error);
                }
            };

            fetchData();
        }, []);

        const handleButtonClick = () => {
            navigate("/writing");
        };

        const formatDate = (dateString) => {
            const date = new Date(dateString);
            return date.toLocaleDateString();
        };

        const handleBoardClick = async (bIdx) => {
            try {
                //  bIdx 
                await axios.get(`/board?bIdx=${bIdx}`);
                //     
                navigate(`/read?bIdx=${bIdx}`);
            } catch (error) {
                console.error(error);
            }
        };

        return (
            <>
                <Header />
                <Navigation />
                <Topic> </Topic>
                <Title>
                    <span> </span>
                    <span> </span>
                    <span></span>
                    <span></span>
                </Title>

                {boardList.map((board, index) => (
                    <Title key={board.bidx} onClick={() => handleBoardClick(board.bidx)}>
                        <div>{board.bidx}</div>
                        <div>
                            {board.btitle}
                        </div>
                        <div>{board.memberVO.mnick}</div>
                        <div>{formatDate(board.insertDate)}</div>
                    </Title>
                ))}

                <Button onClick={handleButtonClick}> </Button>
            </>
        );
    };

    const Topic = styled.div`
    width: 100%;
    height: 3rem;
    background-color: #FFFFE0;
    text-align: center;
    font-size: 3rem;
    border: 3px solid #F5DA81;
    `;

    const Title = styled.div`
    width: 90%;
    height: 4rem;
    font-size: 3rem;
    border: 2px solid black;
    border-radius: 15px;
    margin: 0 auto;
    margin-top: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
    `;

    const Button = styled.button`
    width: 6rem;
    height: 3rem;
    margin-top: 2rem;
    margin-right: 4.5rem;
    cursor: pointer;
    float: right;
    border-radius: 10px;
    border: 2px solid black;
    background-color: skyblue;
    &:hover {
        background-color: #FFFFE0;
    }
    cursor: pointer;
    font-size: 2rem;
    `;

    export default Board;
